// This is taken from https://github.com/bvaughn/react-highlight-words/blob/master/src/Highlighter.js
// At the time of writing, the install isn't working - some react version mismatch thing.

import { findAll } from 'highlight-words-core';
import PropTypes from 'prop-types';
import React from 'react';
import memoizeOne from 'memoize-one';
import { View, Text } from 'react-native';

Highlighter.propTypes = {
  activeClassName: PropTypes.string,
  activeIndex: PropTypes.number,
  activeStyle: PropTypes.object,
  autoEscape: PropTypes.bool,
  className: PropTypes.string,
  findChunks: PropTypes.func,
  highlightClassName: PropTypes.oneOfType([PropTypes.object, PropTypes.string]),
  highlightStyle: PropTypes.object,
  highlightTag: PropTypes.oneOfType([PropTypes.node, PropTypes.func, PropTypes.string]),
  sanitize: PropTypes.func,
  searchWords: PropTypes.arrayOf(
    PropTypes.oneOfType([PropTypes.string, PropTypes.instanceOf(RegExp)])
  ).isRequired,
  textToHighlight: PropTypes.string.isRequired,
  unhighlightClassName: PropTypes.string,
  unhighlightStyle: PropTypes.object
};

/**
 * Highlights all occurrences of search terms (searchText) within a string (textToHighlight).
 * This function returns an array of strings and <span>s (wrapping highlighted words).
 */
export default function Highlighter({
  activeClassName = '',
  activeIndex = -1,
  activeStyle,
  autoEscape,
  caseSensitive = false,
  className,
  findChunks,
  highlightClassName = '',
  highlightStyle = {},
  parentStyle = {},
  highlightTag = 'Text',
  sanitize,
  searchWords,
  textToHighlight,
  unhighlightClassName = '',
  unhighlightStyle
}) {
  const chunks = findAll({
    autoEscape,
    caseSensitive,
    findChunks,
    sanitize,
    searchWords,
    textToHighlight
  });
  const HighlightTag = highlightTag;
  let highlightCount = -1;
  let highlightClassNames = '';
  let highlightStyles;

  const lowercaseProps = object => {
    const mapped = {};
    for (let key in object) {
      mapped[key.toLowerCase()] = object[key];
    }
    return mapped;
  };
  const memoizedLowercaseProps = memoizeOne(lowercaseProps);

  return (
    <Text className={className} style={parentStyle}>
      {chunks.map((chunk, index) => {
        const text = textToHighlight.substr(chunk.start, chunk.end - chunk.start);

        if (chunk.highlight) {
          highlightCount++;

          let highlightClass;
          if (typeof highlightClassName === 'object') {
            if (!caseSensitive) {
              highlightClassName = memoizedLowercaseProps(highlightClassName);
              highlightClass = highlightClassName[text.toLowerCase()];
            } else {
              highlightClass = highlightClassName[text];
            }
          } else {
            highlightClass = highlightClassName;
          }

          const isActive = highlightCount === +activeIndex;

          highlightClassNames = `${highlightClass} ${isActive ? activeClassName : ''}`;
          highlightStyles =
            isActive === true && activeStyle != null
              ? Object.assign({}, highlightStyle, activeStyle)
              : highlightStyle;

          const Highlight = props => (
            <Text style={{ color: 'rgb(238, 122, 177)' }}>{props.children}</Text>
          );
          return (
            <Highlight className={highlightClassNames} key={index} style={highlightStyles}>
              {text}
            </Highlight>
          );
        } else {
          return (
            <Text className={unhighlightClassName} key={index} style={{ color: 'white' }}>
              {text}
            </Text>
          );
        }
      })}
    </Text>
  );
}
